import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { MapResult } from '@schemas/modules/result-map/results-map.model';

@Component({
    selector: 'tz-car-rental-map-empty-popup',
    templateUrl: './car-rental-map-empty-popup.component.html',
    styleUrls: ['./car-rental-map-empty-popup.component.scss'],
})
export class CarRentalMapEmptyPopupComponent implements OnInit {
    @Input() marker: MapResult | undefined | any;
    @Output() searchCars: EventEmitter<void> = new EventEmitter();
    @Output() closePopup: EventEmitter<void> = new EventEmitter();

    constructor() {}

    ngOnInit() {}

    public onCloseButtonClicked() {
        this.closePopup.emit();
    }

    public onSearch(): void {
        this.searchCars.emit();
    }
}
