import {
    AfterViewInit,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnChanges,
    OnInit,
    Output,
    Renderer2,
    SimpleChanges,
    ViewChild,
} from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { CarRental } from '@schemas/modules/car-rentals/car-rental';
import { CarRentalPackage } from '@schemas/modules/car-rentals/car-rental-package';
import { ServiceListComponent } from '@core/components/paginate.component.ts/service.list.component';
import { IInfiniteScrollEvent } from 'ngx-infinite-scroll';
import { CarRentalListMetadata } from '@schemas/modules/car-rentals/car-rental-list-metadata';
import { window } from 'ngx-bootstrap/utils';
import { VirtualScrollerComponent } from 'ngx-virtual-scroller';
import { PlatformService } from '@services/platform.service';
import { ModalService } from '@services/modal.service';
import { MobCarRentalDetailsComponent } from '../mob-car-rental-details/mob-car-rental-details.component';

@Component({
    selector: 'tz-car-rental-list',
    templateUrl: './car-rental-list.component.html',
    styleUrls: ['./car-rental-list.component.scss'],
})
export class CarRentalListComponent extends ServiceListComponent implements OnInit, AfterViewInit {
    @ViewChild('carRentalScroller', { static: false }) virtualScroller: VirtualScrollerComponent | undefined =
        undefined;
    @ViewChild('carRentalListContainer', { static: false }) listContainer: ElementRef | undefined = undefined;
    @Input() count = 0;
    @Input() carRentals: CarRental[] = [];
    @Input() isMobile$?: BehaviorSubject<boolean>;
    @Input() carRentalListMetadata: CarRentalListMetadata = {};
    @Input() isListCompleted = false;
    @Input() selectedCarRentalId = '';
    @Output() nextPage: EventEmitter<void> = new EventEmitter<void>();
    @Output() selectedCarRental: EventEmitter<CarRental> = new EventEmitter<CarRental>();
    @Output() cancelSelection: EventEmitter<void> = new EventEmitter<void>();
    @Output() openPopupModel = new EventEmitter<{ carRental: CarRental; modelType: string }>();
    @Output() showCarRentalDetails: EventEmitter<CarRental> = new EventEmitter<CarRental>();

    public currentSearchJourneyType = 'Onward';
    public selectedCar!: CarRental;

    constructor(
        private renderer: Renderer2,
        public platformService: PlatformService,
        public modalService: ModalService
    ) {
        super();
    }

    ngOnInit(): void {}

    ngAfterViewInit() {
        this.setScrollingContainer();
    }

    public onSelectCarRental(index: number): void {
        this.renderer.setStyle(this.listContainer?.nativeElement, 'height', 'auto');

        const selectedRental: CarRental = JSON.parse(JSON.stringify(this.carRentals[index]));
        selectedRental.packagesAvailable = selectedRental.packagesAvailable.filter(
            (p: CarRentalPackage) => p.notApplicable === false
        );
        this.selectedCar = selectedRental;
        this.selectedCar.currentPackage = selectedRental.packagesAvailable[0];
        this.selectedCarRental.emit(this.selectedCar);
    }

    public selectRentalPackage(rentalPackage: CarRentalPackage): void {
        this.selectedCar.currentPackage = rentalPackage;
        this.selectedCarRental.emit(this.selectedCar);
    }

    public onCancelSelection(): void {
        this.cancelSelection.emit();
        this.selectedCar = {} as CarRental;
    }

    public onScroll($event: IInfiniteScrollEvent) {
        if (!this.isListCompleted) {
            this.nextPage.emit();
        }
    }

    public identify(index: number, item: CarRental): string {
        return item.id;
    }

    public handleScrollOnContainer(double = true): void {
        requestAnimationFrame(() => {
            if (this.virtualScroller) {
                const height =
                    this.virtualScroller?.viewPortInfo.maxScrollPosition +
                    this.virtualScroller?.viewPortInfo.scrollEndPosition -
                    this.virtualScroller.viewPortInfo.scrollStartPosition +
                    24;

                if (this.selectedCar) {
                    this.renderer.setStyle(this.listContainer?.nativeElement, 'height', 'auto');
                } else {
                    this.renderer.setStyle(this.listContainer?.nativeElement, 'height', height + 'px');
                }
                if (double) {
                    this.handleScrollOnContainer(false);
                }
            }
        });
    }

    public setScrollingContainer(): void {
        const element = this.renderer.selectRootElement('.wrapper', true);
        this.scrollingContainer = element ? element : window;
    }

    public async onShowCarDetails(carRental: CarRental, index: number): Promise<void> {
        this.onSelectCarRental(index);
        this.showCarRentalDetails.emit(carRental);
    }
}
