import { Component, ElementRef, EventEmitter, Injector, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ServiceId } from '@core/constants/service-id.const';
import { BehaviorSubject, Observable, Subject, Subscription } from 'rxjs';
import { CarRentalsResponse } from '@schemas/modules/car-rentals/car-rentals-response';
import { CarRental } from '@schemas/modules/car-rentals/car-rental';
import { CarRentalDataService } from '@services/modules/car-rental/dataservices/car-rental.data.service';
import { FilterItem, FilterItemCheckbox, FilterReferenceItem } from '@schemas/modules/core/filters/service-filter';
import { CarRentalSearchQueryParams } from '@schemas/modules/car-rentals/car-rental-search-query-params';
import { BaseServiceComponent } from '../components/base-service-component/base-service.component';
import { ICart } from '@schemas/modules/cart/cart';
import { ListenerId } from '@core/constants/listener-id.const';
import { SortByValueOption } from '@schemas/modules/core/sort-by-value.option';
import { StateFrom } from '@core/constants/state-from.const';
import { finalize, skip, takeUntil, tap } from 'rxjs/operators';
import { CarRentalPackage } from '@schemas/modules/car-rentals/car-rental-package';
import { CarRentalListMetadata } from '@schemas/modules/car-rentals/car-rental-list-metadata';
import { CarRentalService } from '@services/modules/car-rental/services/car-rental.service';
import { CarRentalMapComponent } from './components/car-rental-map/car-rental-map.component';
import { ILocation } from '@schemas/modules/core/location.model';
import { MapResult } from '@schemas/modules/result-map/results-map.model';
import { MobCarRentalDetailsComponent } from './components/mob-car-rental-details/mob-car-rental-details.component';
import { FilterService } from '@services/modules/inactivity/filter.service';
import { SearchUtilsService } from '@services/modules/search/utils/search.utils';
import { ResultsFiltersUtils } from '../results-filters/results-filters.utils.service';

@Component({
    selector: 'tz-car-rentals',
    templateUrl: './car-rentals.component.html',
    styleUrls: ['./car-rentals.component.scss'],
})
export class CarRentalsComponent extends BaseServiceComponent implements OnInit, OnDestroy {
    @ViewChild('carRentalList') carRentalListElementReference?: ElementRef;
    @ViewChild('carRentalListSkeletonLoader') carRentalListSkeletonLoaderElementReference?: ElementRef;
    @ViewChild('confirmationComponent') confirmationComponent?: ElementRef;
    @ViewChild('confirmationComponentReplacer') confirmationComponentReplacer?: ElementRef;
    public journeyType = 'roundTrip';

    public carRentalFilter: FilterReferenceItem = [];
    public carRentals: CarRental[] = [];
    public carRentalListMetadata: CarRentalListMetadata = {};
    public selectedCarRentals: CarRental[] = [];
    public cartData: ICart | undefined;
    public showPackageFlag = false;
    public cachedCarRentalPackages: { [key: string]: CarRentalPackage[] } = {};
    public selectedChangeOptions: EventEmitter<void> = new EventEmitter<void>();
    public sortByOptions: SortByValueOption[] = [
        {
            sort: 'bestValue',
            label: 'GENERAL.SORT.BEST_VALUE',
        },
        {
            sort: 'cheapest',
            label: 'GENERAL.SORT.CHEAPEST',
        },
    ];
    public selectedFiltersCount = 0;
    public filterQueryParams: CarRentalSearchQueryParams = {};
    public serviceTitle = '';
    private readonly serviceId = ServiceId.CAR_RENTAL;
    private carRecommendedID = '';
    private curChange!: Subscription;
    private resultsCount$ = new Subject<number>();
    private repriceListenerId = -1;
    private isGetCarPackagesProcessing = false;

    constructor(
        private carRentalDataService: CarRentalDataService,
        private injector: Injector,
        private carRentalService: CarRentalService
    ) {
        super(injector);
        this.isMobile$ = this.screenSizeDetectorService.isMobile;
        this.isNextSummary = this.nextIsSummary();
    }

    get isEnableClearFilter(): boolean {
        return FilterService.enableClearAll(this.carRentalFilter);
    }

    private static initSearchParams(): any {
        return {
            getNearestLocation: false,
            locationName: '',
            journeyType: 'onwardJourney',
            selectedCarRentals: {},
            sort_by: 'bestValue',
            refreshFilter: false,
        };
    }

    ngOnInit(): void {
        if (this.searchRunning()) {
            this.showSkeletonLoader(true);
        } else {
            this.initializeCarRentalsComponent(true);
        }
    }

    ngOnDestroy(): void {
        this.listenersService.unregisterListener(this.repriceListenerId);
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public onFilterChanged($event: FilterReferenceItem): void {
        this.scrollToServiceContainer();
        this.carRentalFilter = $event;
        this.filterCarRentals();
    }

    public onChangeShowPackages($event: boolean) {
        this.showPackageFlag = $event;
        if ($event) {
            this.checkConfirmability();
        } else {
            this.scrollToTop();
        }
    }

    public sortCarRentals(sortValue: string): void {
        SearchUtilsService.saveSortingValue(ServiceId.CAR_RENTAL, sortValue);
        this.filterQueryParams.sort_by = sortValue;
        this.loadCarRentalData(false);
    }

    public onSelectedCarRental(selectedCarRental: CarRental) {
        this.selectedCarRentals = selectedCarRental ? [selectedCarRental] : [];
        this.checkConfirmability();
    }

    public onCancelSelection(): void {
        this.confirmable = false;
        this.selectedCarRentals = [];
        this.loadCarRentalData(false);
        this.scrollToTop();
    }

    public onChangeCarRental(): void {
        if (this.carRentalListSkeletonLoaderElementReference) {
            this.scrollToElement(this.carRentalListSkeletonLoaderElementReference);
        } else {
            this.scrollToElement(this.carRentalListElementReference);
        }
    }

    public onOpenFilterPopup() {
        const filter: BehaviorSubject<FilterReferenceItem> = new BehaviorSubject(this.carRentalFilter);
        filter.subscribe((filterReferenceItem: FilterReferenceItem) => {
            this.carRentalFilter = filterReferenceItem;
            this.filterCarRentals();
        });
        this.openFilterDialog(filter, this.resultsCount$);
    }

    public addItemToCart(carRental: CarRental): void {
        this.carRentalDataService.addCarRentalToCart([carRental]).subscribe(
            (response: ICart) => {
                this.confirmable = false;
                this.listenersService.NotifyListeners(ListenerId.TOGGLE_RESULTS_SUMMARY, {
                    show: !this.confirmable,
                });
                this.userSearchService.searchCheckDep(ServiceId.CAR_RENTAL, this.isNextSummary, false, false, true);
            },
            (error: any) => {
                console.log(error);
            }
        );
    }

    public onGetPackagesOfTheCarRental(carRental: CarRental): void {
        this.getPackagesOfTheCarRental(carRental).subscribe((packages: CarRentalPackage[]) => {
            this.scrollToConfirmationBlock();
        });
    }

    public getPackagesOfTheCarRental(carRental: CarRental): Observable<CarRentalPackage[]> {
        this.isGetCarPackagesProcessing = true;
        return this.carRentalDataService.getCarRentalPackages(carRental).pipe(
            tap((packages: CarRentalPackage[]) => {
                this.isGetCarPackagesProcessing = false;
                this.cachedCarRentalPackages[carRental.id] = packages;
            }),
            takeUntil(this.destroy$)
        );
    }

    public onRemoveSelectedCarRental(optionId: string | undefined): void {
        this.removeService(this.serviceId, optionId).then(
            (isRemoved: boolean) => {
                console.log('remove success');
            },
            (error) => {
                console.error(error);
            }
        );
    }

    public onSubmitBooking(): void {
        if (this.selectedCarRentals[0]) {
            this.addItemToCart(this.selectedCarRentals[0]);
        }
    }

    public nextPage(): void {
        if (!this.loading) {
            this.loading = true;
            const params = {
                start: this.start,
                limit: this.carRentalListMetadata.increment,
                sortBy: this.filterQueryParams.sort_by,
            };
            this.carRentalDataService
                .paginate(this.serviceId, params)
                .pipe(
                    finalize(() => {
                        this.loading = false;
                    })
                )
                .subscribe((carRentals: CarRental[]) => {
                    this.carRentals = [...this.carRentals, ...carRentals];
                    // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
                    if (carRentals.length < this.carRentalListMetadata.increment!) {
                        this.isListCompleted = true;
                    } else {
                        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
                        this.start += this.carRentalListMetadata.increment!;
                    }
                });
        }
    }

    public onOpenPopupModel(rentalModel: { carRental: CarRental; modelType: string }) {
        this.carRentalService.openPopupModel(rentalModel);
    }

    public onCarRentalMapOpen(): void {
        const carRentalData = this.searchDataService.getSearchData(this.serviceId);
        if (!carRentalData || !carRentalData.address) {
            return;
        }

        const mapCenterPosition = {
            lng: carRentalData.to?.longitude,
            lat: carRentalData.to?.latitude,
        };

        const mapConf = {
            id: 'carRentalMapDialog',
            height: '100%',
            width: '100%',
            maxWidth: '100vw',
            panelClass: 'tz-results-map',
            data: {
                mapCenterPosition,
                serviceId: this.serviceId,
            },
        };

        const dialogRef = this.dialogService.openAccommodationDialog(CarRentalMapComponent, mapConf);

        dialogRef.afterClosed().subscribe((marker: MapResult) => {
            if (marker) {
                if (marker) {
                    this.filterAccordingLocation(marker);
                }
            }
        });

        dialogRef.componentInstance.searchClicked.subscribe((marker: any) => {
            this.searchNewLocation(marker);
        });
    }

    public onOpenFilterModal(): void {
        const filters: BehaviorSubject<FilterReferenceItem> = new BehaviorSubject(this.carRentalFilter);
        filters.pipe(takeUntil(this.destroy$)).subscribe((filter: FilterReferenceItem) => {
            this.carRentalFilter = filter;
            this.filterCarRentals();
        });
        this.openFilterModal(filters, this.resultsCount$);
    }

    public onResetFilter(): void {
        const filters$: Subject<FilterReferenceItem> = new Subject();
        filters$.pipe(takeUntil(this.destroy$)).subscribe((filter: FilterReferenceItem) => {
            this.carRentalFilter = filter;
            this.filterCarRentals();
        });
        BaseServiceComponent.resetFilter(this.carRentalFilter, filters$);
    }

    public mobOpenSort(): void {
        const sort$: Subject<string> = new Subject<string>();
        sort$.pipe(takeUntil(this.destroy$)).subscribe((sortKey: string) => {
            this.sortCarRentals(sortKey);
        });

        this.openSortMob(this.sortByOptions, this.filterQueryParams.sort_by, sort$);
    }

    public showCarRentalDetails(carRental: CarRental): void {
        const openPopupModel$: Subject<{
            carRental: CarRental;
            modelType: string;
        }> = new Subject<{
            carRental: CarRental;
            modelType: string;
        }>();

        openPopupModel$.pipe(takeUntil(this.destroy$)).subscribe((data) => {
            this.onOpenPopupModel(data);
        });

        this.modalService
            .createDetailsPage(MobCarRentalDetailsComponent, {
                data: { carRental, openPopupModel$, type: 'details' },
                title: 'POPUPS.CAR_RENTAL.DETAILS_TITLE',
                modalCustomClasses: ['mobile-car-rental-details-background'],
            })
            .then((modal) => {
                if (modal.data) {
                    this.selectedCarRentals = [];
                    this.selectedCarRentals.push(modal.data);
                    this.onSubmitBooking();
                }

                if (!modal.data && carRental.packagesAvailable.length > 1) {
                    carRental.currentPackage = carRental.packagesAvailable[0];
                }
            });
    }

    showSelectedCarRentalDetails(carRental: CarRental) {
        if (this.isGetCarPackagesProcessing) {
            return;
        }

        this.getPackagesOfTheCarRental(carRental).subscribe((packages: CarRentalPackage[]) => {
            carRental.packagesAvailable = packages;
            this.showCarRentalDetails(carRental);
        });
    }

    private loadCarRentalData(checkConsistency: boolean): void {
        this.checkResultsAvailable(this.serviceId);
        this.loaderService.skeletonLoading = true;
        const params = CarRentalsComponent.initSearchParams();
        params.sort_by = this.filterQueryParams.sort_by;
        this.carRentalDataService.searchServiceData(this.serviceId, params, checkConsistency).subscribe(
            (carRentalsResponse: CarRentalsResponse) => {
                this.handleCarRentalResponse(carRentalsResponse);
                this.loaderService.skeletonLoading = false;
                this.handleExtraParams();

                if (!carRentalsResponse.rentals.count && !this.filterCount(carRentalsResponse.ogs)) {
                    this.carRentalDataService.checkServiceFailedAndNotify(this.serviceId);
                }
                setTimeout(() => {
                    console.log('set prerender ready');
                    window.prerenderReady = true;
                }, 1500);
            },
            (error: any) => {
                //this.loaderService.skeletonLoading = false;
            }
        );
    }

    private filterCarRentals(): void {
        this.loaderService.skeletonLoading = true;
        this.isListCompleted = false;
        this.carRentalDataService
            .filterServiceData(this.serviceId, this.filterQueryParams, this.carRentalFilter)
            .subscribe(
                (carRentalsResponse: CarRentalsResponse) => {
                    this.handleCarRentalResponse(carRentalsResponse);
                    this.loaderService.skeletonLoading = false;
                },
                (error) => {
                    this.loaderService.skeletonLoading = false;
                }
            );
    }

    private checkConfirmability() {
        this.confirmable = this.carRentals.length > 0;
        if (this.confirmable) {
            this.scrollToConfirmationBlock();
        }
    }

    private nextIsSummary(): boolean {
        const state = this.router.getCurrentNavigation()?.extras?.state;
        const from = state ? state['from'] : '';
        return from === StateFrom.SUMMARY || from === StateFrom.TAB;
    }

    private getCartServiceData() {
        this.cartData = this.carRentalDataService.getServiceCartItems();
    }

    private handleCarRentalResponse(carRentalsResponse: CarRentalsResponse): void {
        this.carRentalFilter = carRentalsResponse.ogs;
        this.carRecommendedID = carRentalsResponse.bestValueRental;

        this.handlingCarRentalsData(carRentalsResponse.rentals.data);

        this.selectedFiltersCount = ResultsFiltersUtils.selectedFiltersCounter(this.carRentalFilter);

        const { count, limit, limiter, increment } = carRentalsResponse.rentals;
        this.start = limiter;
        if (count < limit) {
            this.isListCompleted = true;
        }
        this.resultsCount$.next(count);
        this.carRentalListMetadata = {
            count,
            limiter,
            increment,
        };
        if (carRentalsResponse.showMap) {
            this.onCarRentalMapOpen();
        }
    }

    private initializeCarRentalsComponent(checkConsistency: boolean) {
        this.filterQueryParams = { sort_by: 'cheapest' };
        this.serviceTitle = this.formServiceTitle(ServiceId.CAR_RENTAL);
        this.setSortingValue();
        this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
            this.serviceTitle = this.formServiceTitle(ServiceId.CAR_RENTAL);
            this.loadCarRentalData(false);
        });
        this.curChange = this.currencyService.selectedCurrencySubject
            .pipe(skip(1), takeUntil(this.destroy$))
            .subscribe(() => {
                this.loadCarRentalData(false);
            });
        this.repriceListenerId = this.listenersService.registerListener(
            ListenerId.LISTEN_REPRICED,
            this.getCartServiceData,
            this
        );
        this.loadCarRentalData(checkConsistency);
        this.getCartServiceData();
        this.checkCarRentalMapPopup();
    }

    private handlingCarRentalsData(carRentals: CarRental[]): void {
        const cartData = this.cartData;
        const cartCarRentalID = cartData && cartData.items.length ? cartData.items[0].selectedItem.id : null;
        this.carRentals = this.carRentalDataService.handlingData<CarRental>(
            carRentals,
            this.carRecommendedID,
            cartCarRentalID
        );
        this.carRentals = this.filterCarRentalsData(carRentals);
    }

    private filterCarRentalsData(carRentals: CarRental[]): CarRental[] {
        const cartId = this.cartData?.items[0] ? this.cartData?.items[0].selectedItem.id : null;
        if (cartId) {
            return this.carRentalDataService.filterDataRemoveCartItem<CarRental>(carRentals, cartId);
        }

        return carRentals;
    }

    private setSortingValue(): void {
        const sort = SearchUtilsService.getSortingValue(ServiceId.CAR_RENTAL);

        if (sort) {
            this.filterQueryParams.sort_by = sort;
        }
    }

    private searchNewLocation(marker: any) {
        if (!marker.point.latitude && !marker.point.longitude) {
            return;
        }
        const searchData = this.searchDataService.getSearchData(ServiceId.CAR_RENTAL);

        if (searchData) {
            if (searchData.to) {
                searchData.to.latitude = marker.point.latitude;
                searchData.to.longitude = marker.point.longitude;
                searchData.to.locationID = marker.locationID;
                searchData.to.formattedAddress = marker.address;
                searchData.to.supplierName = marker.supplierName;
                searchData.to.featureKey = marker.featureKey;
                searchData.address = {} as ILocation;
                searchData.retainSearch = true;
            }

            const searchInfos = this.searchDataService.getSearch();
            this.loaderService.triggerLoading.emit({
                loading: true,
                type: 'small',
                textKey: this.translateService.instant('LOADER_MESSAGES.SEARCH_OPTIONS'),
                title: this.translateService.instant('LOADER_MESSAGES.CHECKING'),
            });
            this.userSearchService.search(
                {
                    bookedServiceId: ServiceId.CAR_RENTAL,
                    serviceId: ServiceId.CAR_RENTAL,
                    editMode: true,
                },
                {
                    searchData,
                    context: searchInfos.context,
                    passengers: searchInfos.passengers,
                    //retainSearch: true,
                }
            );
        }
    }

    private checkCarRentalMapPopup() {
        const searchData = this.searchDataService.getSearchData(this.serviceId);
        if (searchData && searchData.retainSearch) {
            const dialog = this.dialogService.getDialogById('carRentalMapDialog');
            if (dialog && dialog.componentInstance && dialog.componentInstance instanceof CarRentalMapComponent) {
                this.loaderService.triggerLoading.emit({ loading: false });
                dialog.componentInstance.resetData();
                dialog.afterClosed().subscribe((marker: MapResult) => {
                    if (marker) {
                        if (marker) {
                            this.filterAccordingLocation(marker);
                        }
                    }
                });
            }
        }
        //do not show map when user already selected a car
        if (
            (!this.cartData || !this.cartData.items || this.cartData.items[0].isVirtual) &&
            (this.searchDataService.isMap(this.serviceId) || searchData?.retainSearch)
        ) {
            this.onCarRentalMapOpen();
        }
    }

    private filterAccordingLocation(marker: MapResult) {
        const locationFilter = this.carRentalFilter.find(
            (filterItem: FilterItem) => filterItem.ref_id === 'CAR_RENTAL.LOCATION'
        );
        if (locationFilter && locationFilter.checkboxes) {
            const checkboxReferenceObject = locationFilter?.checkboxes.find((checkBox: FilterItemCheckbox) => {
                const formedLocationId = 'LOCATIONS' + (marker as any).locationID;
                return formedLocationId === checkBox.ref_id;
            });
            if (checkboxReferenceObject) {
                checkboxReferenceObject.selected = true;
                this.filterCarRentals();
            }
        }
    }

    private handleExtraParams() {
        const state = history.state;
        if (state && state.extra) {
            for (const extraAction in state.extra) {
                if (state.extra.hasOwnProperty(extraAction)) {
                    switch (extraAction) {
                        case 'changeOptions':
                            if (this.cartData && this.cartData.items) {
                                if (this.platformService.isMobileDevice) {
                                    this.showSelectedCarRentalDetails(this.cartData.items[0].selectedItem.data);
                                } else {
                                    this.selectedChangeOptions.emit();
                                }
                                this.scrollToConfirmationBlockReplacer();
                            }
                            break;
                        case 'changeTickets':
                            this.onChangeCarRental();
                            break;
                    }
                }
            }
        }
    }

    private scrollToConfirmationBlock(): void {
        setTimeout(() => {
            requestAnimationFrame(() => {
                requestAnimationFrame(() => {
                    this.scrollToElement(this.confirmationComponent, { block: 'end' });
                });
            });
        }, 500);
    }

    private scrollToConfirmationBlockReplacer(): void {
        setTimeout(() => {
            requestAnimationFrame(() => {
                requestAnimationFrame(() => {
                    this.scrollToElement(this.confirmationComponentReplacer, { block: 'end' });
                });
            });
        }, 500);
    }
}
