import { Component, EventEmitter, Inject, Injector, OnInit, Output } from '@angular/core';
import { TzMapComponent } from '../../../../map/map.component';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ResultService } from '@services/modules/core/dataservices/result.service';
import { TzCurrencyPipe } from '@shared/pipes/tz-currency.pipe';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { MapResult } from '@schemas/modules/result-map/results-map.model';
import { Map, Marker, ScaleControl } from 'mapbox-gl';
import { DialogService } from '@services/dialog.service';
import { TranslateService } from '@ngx-translate/core';
import { CartService } from '@services/modules/cart/services/cart.service';

@Component({
    selector: 'tz-car-rental-map',
    templateUrl: './car-rental-map.component.html',
    styleUrls: ['./car-rental-map.component.scss'],
})
export class CarRentalMapComponent extends TzMapComponent implements OnInit {
    @Output() searchClicked = new EventEmitter<any>();

    public markers: MapResult[] = [];
    public selectedCarRental: MapResult | null = null;
    public hoveredCarRental: MapResult | null = null;
    public showSelectedPopUp = false;
    public showHoveringPopup = false;
    public showHotelHoveringPopup = false;
    public hotelMarkerSelected = false;
    public hotelMarker: any = null;

    constructor(
        injector: Injector,
        @Inject(MAT_DIALOG_DATA) data: any,
        mdDialogRef: MatDialogRef<TzMapComponent>,
        resultService: ResultService,
        tzCurrencyPipe: TzCurrencyPipe,
        searchDataService: SearchDataService,
        dialogService: DialogService,
        private translateService: TranslateService,
        private cartService: CartService
    ) {
        super(injector, data, mdDialogRef, resultService, tzCurrencyPipe, searchDataService, dialogService);
    }

    public override ngOnInit() {
        this.initiateMapComponent();
    }

    public resetData(): void {
        this.fetchMapData(true);
    }

    public override onMapLoad(map: Map): void {
        this.mapboxRef = map;
        this.mapboxRef.addControl(
            new ScaleControl({
                maxWidth: 80,
                unit: 'metric',
            })
        );

        const data = this.searchDataService.getSearchData(this.mapConfig.serviceId);
        if (data && data.address) {
            const coordinates = {
                lng: data.to?.longitude ? data.to?.longitude : data.address.longitude,
                lat: data.to?.latitude ? data.to?.latitude : data.address.latitude,
            };
            this.checkForHotelMarker();
            if (!this.hotelMarker) {
                if (data.to) {
                    new Marker({ element: this.createMarker(undefined, 'red-marker') })
                        .setLngLat(coordinates)
                        .addTo(map);
                }
            }
        }

        const mapResults = this.mapResults as any;
        const markerLoaded = this.mapResults && mapResults.suppliers && mapResults.suppliers.length !== 0;
        if (markerLoaded) {
            this.updateMarkersOnTheMap();
        }

        this.addMapEvents();
    }

    public onPopUpBodyClicked($event: MouseEvent) {
        $event.stopPropagation();
    }

    public onMarkerMouseOver(marker: MapResult) {
        if (this.selectedCarRental === marker) {
            return;
        }
        this.hoveredCarRental = marker;
        this.showHoveringPopup = true;
    }

    public onHotelMarkerMouseOver() {
        this.showHotelHoveringPopup = true;
    }

    public onHotelMarkerMouseLeave() {
        this.showHotelHoveringPopup = false;
    }

    public onHotelMarkerClick($event: MouseEvent) {
        $event.stopPropagation();
        this.hotelMarkerSelected = true;
    }

    public onMarkerClick($event: MouseEvent, marker: MapResult) {
        $event.stopPropagation();
        this.selectedCarRental = marker;
        this.hotelMarkerSelected = false;
        if (this.hoveredCarRental === marker) {
            this.hoveredCarRental = null;
            this.showHoveringPopup = false;
        }
        this.showSelectedPopUp = true;
    }

    public onCloseSelectedMarkerPopup() {
        this.hotelMarkerSelected = false;
        this.showHotelHoveringPopup = false;
        this.selectedCarRental = null;
        this.showSelectedPopUp = false;
    }

    public onMouseLeaveMarker(marker: MapResult) {
        if (this.selectedCarRental === marker) {
            return;
        }
        this.hoveredCarRental = null;
        this.showHoveringPopup = false;
    }

    public onSearchLocation(marker: MapResult): void {
        this.dialogService.closeDialog(this.mdDialogRef, marker);
    }

    public onSearchNewLocation(marker: MapResult | null) {
        if (marker) {
            this.searchClicked.emit(marker);
        }
    }

    protected override fetchMapData(update?: boolean): void {
        const queryParams = { selected_id: this.selectedId };
        const request = { ...this.mapCorners, center: {} };

        this.resultService.searchMapResults(this.mapConfig.serviceId, queryParams, request).subscribe((results) => {
            this.mapResults = results;
            if (this.mapboxRef) {
                this.updateMarkersOnTheMap(update);
            }
        });
    }

    protected override initiateMapComponent(): void {
        this.mapCorners = {
            topLeft: {
                lat: this.mapConfig.mapCenterPosition.lat + 0.055968722,
                lng: this.mapConfig.mapCenterPosition.lng - 0.111450424,
            },
            bottomRight: {
                lat: this.mapConfig.mapCenterPosition.lat - 0.066414125,
                lng: this.mapConfig.mapCenterPosition.lng + 0.105686722,
            },
        };

        if (this.mapConfig.selectedHotelId) {
            this.selectedId = this.mapConfig.selectedHotelId;
        }
        this.checkForHotelMarker();
        this.fetchMapData();
    }

    private updateMarkersOnTheMap(update?: boolean) {
        this.markers = this.mapResults.suppliers;
        if (this.selectedCarRental) {
            const selectedCarRental = this.markers.find(
                (marker: MapResult) => marker.locationID === this.selectedCarRental?.locationID
            );
            if (selectedCarRental) {
                this.selectedCarRental = selectedCarRental;
            } else {
                if (update) {
                    this.dialogService.openConfirmationDialog({
                        title: this.translateService.instant('POPUPS.NO_RESULTS_FOR_MARKER.TITLE'),
                        content: this.translateService.instant('POPUPS.NO_RESULTS_FOR_MARKER.BODY'),
                        confirmBtnTitle: this.translateService.instant('GENERAL.OK'),
                        icon: { name: 'no-rental-dialog', iconStyle: { 'width.px': 88.23, 'height.px': 57.81 } },
                    });
                }
                this.selectedCarRental = null;
            }
        }
    }

    private checkForHotelMarker() {
        const cart = this.cartService.getCartData();
        if (cart && cart.items.length > 0) {
            const hotelCartItems = cart.items.filter((el: any) => el.dataType === 'hotel');
            if (hotelCartItems.length > 0) {
                const hotelInfo = hotelCartItems[0].selectedItem.data;
                this.hotelMarker = {
                    coordinates: [hotelInfo.location.longitude, hotelInfo.location.latitude],
                    properties: hotelInfo,
                };
            }
        }
    }
}
