import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { MapResult } from '@schemas/modules/result-map/results-map.model';
import { TzCurrencyPipe } from '@shared/pipes/tz-currency.pipe';

@Component({
    selector: 'tz-car-rental-hotel-marker-popup',
    templateUrl: './car-rental-hotel-marker-popup.component.html',
    styleUrls: ['./car-rental-hotel-marker-popup.component.scss'],
})
export class CarRentalHotelMarkerPopupComponent implements OnInit {
    @Input() hotelMarker: any;
    public params: any;

    constructor(private tzCurrencyPipe: TzCurrencyPipe) {}

    ngOnInit() {
        const imageSize = this.hotelMarker.properties.imageProvider === 'Ostrovok' ? '240x240' : 'medium';
        let starsStr = '';
        for (let i = 0; i < this.hotelMarker.properties.star; i++) {
            starsStr += '<img src="/assets/icons/web/icons/star.svg" alt="">';
        }

        this.params = {
            image: this.hotelMarker.properties.imageUrl.replace('{size}', imageSize),
            name:
                this.hotelMarker.properties.name.length > 30
                    ? this.hotelMarker.properties.name.substring(0, 30) + '... '
                    : this.hotelMarker.properties.name,
            starsStr,
            address: this.hotelMarker.properties.address,
            price: this.tzCurrencyPipe.transform(
                this.hotelMarker.properties.cheapestPrice.listPrice,
                this.hotelMarker.properties.cheapestPrice.listPriceCur
            ),
        };
    }

    numberReturn(length: number): any[] {
        return new Array(length);
    }
}
