import { Component, Output, EventEmitter } from '@angular/core';

@Component({
    selector: 'tz-confirm-selection',
    templateUrl: './confirm-selection.component.html',
    styleUrls: ['./confirm-selection.component.scss'],
})
export class ConfirmSelectionComponent {
    @Output()
    whenConfirm: EventEmitter<void> = new EventEmitter<void>();
    @Output()
    whenCancel: EventEmitter<void> = new EventEmitter<void>();

    confirmSelection(): void {
        this.whenConfirm.emit();
    }

    cancelSelection(): void {
        this.whenCancel.emit();
    }
}
